.create {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;

    .create_info {
        position: relative;
        width: 100%;
        color: white;
        margin-bottom: 5rem;
        text-align: center;

        h1 {
            font-size: 3rem;
        }

        p {
            position: absolute;
            bottom: -45px;
            left: 50%;
            transform: translateX(-50%);
            opacity: .5;
            
        }
    }
    
    .create-btn {
        position: relative;
        width: 300px;
        height: 300px;
        border: 5px solid rgb(44, 44, 44);
        border-radius: 5px;
        background-color: transparent;
        transition: .2s all linear;

        &:after {
            position: absolute;
            content: '';
            top: 15%;
            right: 50%;
            z-index: 2;

            width: 20px;
            height: 70%;
            border-radius: 5px;
            transform: translateX(50%);

            background-color: rgb(44, 44, 44);
        }

        
        &:before {
            position: absolute;
            content: '';
            top: 50%;
            right: 15%;
            z-index: 2;

            width: 70%;
            height: 20px;
            border-radius: 5px;
            transform: translateY(-50%);

            background-color: rgb(44, 44, 44);
        }

        &:hover {
            background-color: rgb(10, 10, 10);
        }
    }
}