.pattern-services {

    .pattern-services-header {
        width: 100%;
        background-color: #fff;

        .pattern-services-footer {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background-color: green;
            height: 30px;
            padding: 0 1rem;
        }

        .pattern-services-navbar {
            color: #000;
            padding: 2rem;
            background-color: #fff;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }

    .pattern-services-content {
        height: 666px;
        color: #fff;
        font-size: 3rem;
        background-color: rgb(104, 101, 101);
        display: flex;
        justify-content: center;
        align-items: center;
    }
}