.page {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fff;

    .header {
        width: 100%;
        .header_footer {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: #32C407;
            background: -webkit-linear-gradient(bottom right, #32C407, #265715);
            background: -moz-linear-gradient(bottom right, #32C407, #265715);
            background: linear-gradient(to top left, #32C407, #265715);
            padding: 0.5rem 3rem;

            .logo {
                width: 50px;
            }

            .location {
                display: flex;
                align-items: center;
                color: rgb(230, 188, 4);
                svg {
                    width: 10px;
                    margin-right: 1rem;
                    path {
                        fill: rgb(230, 188, 4);
                    }
                }
            }

            .connect {
                display: flex;
                align-items: center;
                color: #fff;
                svg {
                    width: 20px;
                    margin-right: 1rem;
                    path {
                        fill: #fff;
                    }
                }
            }
        }

        .nav {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0rem 3rem;

            .burger {
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 30px;
                height: 20px;
                cursor: pointer;

                button {
                    width: 100%;
                    height: 2px;
                    background-color: rgb(0, 0, 0);
                    border: none;

                    &:after, &:before {
                        position: absolute;
                        width: 100%;
                        height: 2px;
                        left: 0;
                        content: '';
                        z-index: 2;
                        background-color: rgb(0, 0, 0);
                    }

                    &:after {
                        top: 0;
                    }

                    &:before  {
                        bottom: 0;
                    }
                }
            }

            .nav_link {
                display: flex;
                justify-content: center;
                align-items: center;

                li {
                    color:rgb(53, 53, 53);
                    font-size: 1.5rem;
                    margin-right: 2rem;
                    &:last-child {
                        margin-right: 0;
                    }
                }
            }

            .search_login {
                display: flex;
                justify-content: center;
                align-items: center;
                color: #32C407;

                img {
                    height: 20px;
                    margin-right: 2rem;
                    padding: 2rem 2rem;
                    border-right: 1px solid #000;
                    border-left: 1px solid #000;
                    cursor: pointer;
                }
            }
        }
    }

    .container {
        background-image: url('https://imgholder.ru/600x300/8493a8/adb9ca&text=IMAGE+HOLDER&font=kelson');
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;

        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 4rem;

        svg {
            width: 20px;
        }

        .photo {
            position: relative;
            margin-left: 10rem;
            margin-right: 5rem;
            img { 
                width: 600px;
            }

            .photo_text {
                position: absolute;
                bottom: 5rem;
                left: 50%;
                transform: translateX(-50%);
                font-size: 1.7rem;
                p {

                    &:last-child {
                        margin-top: 1rem;
                        opacity: .5;
                    }
                }
            }
        }

        .container_text {
            margin-right: 10rem;

            h1 {
                color: black;
                font-size: 4rem;
                margin-bottom: 3rem;
            }

            p {
                line-height: 1.5;
                font-size: 1.6rem;
                margin-bottom: 3rem;
            }

            .form {
                textarea {
                    width: 100%;
                    min-height: 150px;
                    max-height: 150px;
                }

                .form_text {
                    display: flex;
                    margin-top: 3rem;

                    .btn_form {
                        background: #32C407;
                        background: -webkit-linear-gradient(bottom right, #32C407, #265715);
                        background: -moz-linear-gradient(bottom right, #32C407, #265715);
                        background: linear-gradient(to top left, #32C407, #265715);
                        color: #fff;
                        padding: 1rem 2rem;
                        border: none;
                        margin-right: 2rem;
                    }

                    p {
                        line-height: 1.4;
                        margin-bottom: 0;
                    }

                    div {
                        margin-right: 2rem;
                        &:last-child {
                            margin-bottom: 0;
                        }
                    }

                    .connect {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                }
            }
        }
    }
}