.page {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;

    .header {
        width: 100%;
        background-color: #fff;

        .header_footer {
            display: flex;
            justify-content: space-between;
            background-color: #d4d4d4;
            padding: 1rem;

            .footer_location {
                display: flex;
                align-items: center;
                justify-content: center;

                img {
                    width: 1rem;
                    margin-right: 5px;
                }

                .location_text {
                    font-size: 1.5rem;
                    color: #000;
                }
            }

            .footer_connection {
                display: flex;

                div {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-right: 3rem;
                    &:last-child {
                        margin-right: 0rem;
                    }
                }

                img {
                    width: 1.5rem;
                    margin-right: 5px;
                }

                .connection_text {
                    color: #000;
                    font-size: 1.5rem;
                }
            }

            .footer_socials {
                display: flex;
                a {
                    margin-right: 1rem;
                    img {
                        width: 2rem;
                        
                    }
                }
            }
        }
    }
}

.header_info {
    display: flex;
    justify-content: space-between;
    padding: 1rem 3rem;

    .logo img {
        width: 100px;
    }

    .nav {
        display: flex;
        align-items: center;
        justify-content: center;

        p {
            color: red;
            font-weight: 600;
            font-size: 1.5rem;
        }

        div {
            position: relative;
            z-index: 4;
            cursor: pointer;
            width: 40px;
            height: 40px;
            margin-left: 2rem;
            .burger_nav {
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                display: flex;
                align-items: center;
                justify-content: center;
                width: 40px;
                height: 2px;
                background-color: red;
                border: none;
    
                &:before, &:after {
                    position: absolute;
                    content: '';
                    left: 0;
                    width: 40px;
                    height: 2px;
                    background-color: red;
                }
    
                &:after {
                    top: -1rem;
                }
    
                &:before {
                    bottom: -1rem;
                }
            }
        }
    }
}

.container_intro {
    width: 100%;
    background: url(https://imgholder.ru/1600x600/8493a8/adb9ca&text=IMAGE+HOLDER&font=kelson);
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    padding: 20rem 0rem;
    .container_intro_text {

        .container_heading {
            text-align: left;
            font-size: 5rem;
            margin-bottom: 3rem;
        }

        .info_block {
            display: flex;
            justify-content: space-between;

            .info_item {
                width: 300px;
                padding: 1rem;
                margin-right: 3rem;
                background-color: #fff;
                color: red;
                text-transform: uppercase;
                font-size: 1.5rem;
                line-height: 1.5;
                border-radius: 5px;
                &:last-child{
                    margin-right: 0rem;
                }
            }
        }
    }
}

.container_about {
    display: flex;
    margin-top: 5rem;
    color: #000;
    padding: 0 5rem;
    padding-bottom: 3rem;

    .about {
        width: 65%;
        margin-right: 2rem;
        .about_heading {
            font-size: 4rem;
            font-weight: 700;
            border-bottom: 1px solid rgb(175, 175, 175);
            padding-bottom: 2rem;
            margin-bottom: 2rem;
        }

        .about_text {

            p {
                font-size: 1.5rem;
                margin-bottom: 2rem;
                line-height: 1.5;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }

    .images {

        .images_item {
            margin-bottom: 2rem;
            &:last-child {
                margin-bottom: 0;
            }

            img {
                margin-right: 2rem;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}