.pattern-store {

    .pattern-store-header {
        width: 100%;
        background-color: #fff;

        .store-header-footer {
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: #000;
            padding: 1rem;
            border-bottom: 1px solid red;
        }

        .store-header-navbar {
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: #000;
            padding: 2rem;

        }
    }

    .pattern-store-main {
        height: 659px;
        background-color: #fff;
        display: flex;
        border-top: 1px solid rgb(155, 155, 155);

        .pattern-store-sidebar {
            width: 200px;
            border-right: 1px solid red;

            .store-sidebar-header {
               background-color: red;
               padding: 1rem;
               text-align: left;
               text-transform: uppercase;
               font-weight:500;
        
            }

            .store-sidebar-catalog {

                li {
                    color: #000;
                    background-color: rgb(223, 223, 223);
                    cursor: pointer;
                    text-align: left;
                    padding: 1rem 0;
                    border-bottom: 1px solid rgb(155, 155, 155);

                    a {
                        padding-left: 1rem;
                    }

                    &:hover {

                        a {
                            color: red;
                            text-decoration: underline;
                        }
                    }
                }
            }
        }

        .pattern-store-content {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #000;
            font-size: 3rem;
        }
    }
}