$fs-nav: 1.6rem;
$color-item: #fff;

.nav {
    display: flex;
    font-size: $fs-nav;
    font-weight: 600;
    color: $color-item;

    .item {
        position: relative;
        cursor: pointer;
        padding: 1rem 1.5rem;

        &:hover {
            background-color: rgb(0, 0, 0);
        }

        &:hover .item-list {
            display: block;
            background-color: rgb(0, 0, 0);
        }

        .item-list {
            position: absolute;
            top: 3.5rem;
            left: 0;
            z-index: 3;
            display: none;
            background: none;
            width: 200px;

            li {
                padding: 1rem;
                
                &:hover {
                    background-color: rgb(12, 12, 12);
                }
            }
        }
    }
}