.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow: hidden;
    background: #0D0F0E;
    background: -webkit-linear-gradient(top, #0D0F0E, #1C1C1B);
    background: -moz-linear-gradient(top, #0D0F0E, #1C1C1B);
    background: linear-gradient(to bottom, #0D0F0E, #1C1C1B);

    .container {
        display: flex;
        position: relative;

        .btn-page-block {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            left: 25rem;
            z-index: 2;

            p {
                color: rgb(37, 37, 37);
                margin-left: 1rem;
            }
    
                
            .show-sidebar {
                position: relative;
                background-color: transparent;
                width: 20px;
                height: 20px;
                border: none;
    
                &:after {
                    position: absolute;
                    top: 35%;
                    left: 50%;
                    content: '';
                    width: 7px;
                    height: 7px;
                    border-bottom: 2px solid #08FDD8;
                    border-right: 2px solid #08FDD8;
                    transform: translate(-50%) rotate(-225deg);
                }
    
                &:before {
                    position: absolute;
                    top: 50%;
                    left: 70%;
                    content: '';
                    width: 15px;
                    height: 2px;
                    background-color: #08FDD8;
                    transform: translate(-50%);
                }

                &.active {
                    &:after {
                        left: 50%;
                        transform: translate(-50%) rotate(-45deg);
                    }
        
                    &:before {
                        left: 30%;
                    }
                }
            }

            &.active {
                left: 1rem;
            }
        }
    }
}