.sidebar {
    width: 400px;
    height: 100vh;
    background: rgba(12, 12, 12, .5);
    padding: 1rem 0;
    overflow-y: auto;

    &.close {
        width: 0;
    }

    h1 {
        opacity: .7;
        color: white;
        margin-bottom: 2rem;
    }

    .save-pages {
        position: relative;
        h2 {
            margin-bottom: 1rem;
        }

        margin-bottom: 2rem;
        padding: 1rem;
        border: 1px solid rgb(90, 90, 90);
        cursor: pointer;

        &:hover {
            border-color: aqua;
        }

        button {
            position: absolute;
            top: 50%;
            right: 1rem;
            transform: translateY(-50%);
            background-color: transparent;
            border: none;
        }

        svg {
            width: 20px;
            cursor: pointer;
        }
    }

    &::-webkit-scrollbar {
        width: 4px; /* ширина для вертикального скролла */
        height: 8px; /* высота для горизонтального скролла */
        background-color: transparent;
    }
    
    /* ползунок скроллбара */
    &::-webkit-scrollbar-thumb {
        background-color: aqua;
        border-radius: 9em;
    }
}
