.alert-message {
    position: absolute;
    display: block;
    top: 0;
    right: 0%;
    z-index: 2;
    padding: 0.5rem 1rem;
    border: 1px solid #fff;
    border-radius: 5px;
    transition: transform .2s linear;

    &.null {
        background-color: green;
        transform: translateX(100%);
    }

    &.error {
        background-color: red;
    }

    div {
        display: flex;
        justify-content: center;
        align-items: center;
        
        img {
            width: 30px;
            margin-right: 1rem;
        }
    }
}