
.model {

    li {
        width: 100%;
        height: 100%;
        list-style-type: none;
        padding: 1rem 0;

        label {
            color: #08FDD8;
            font-size: 1.8rem;
            margin-left: 1rem;
        }

        &:last-child {
            margin-bottom: 0;
        }

        .model-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0.5rem 1rem;

            .btn-select {
                background-color: transparent;
                color: red;
                border: none;
                font-weight: 800;
                margin-right: 2rem;
                font-size: 2rem;

                &:first-child {
                    margin-right: 0rem;
                }
            }

            span {
                font-size: 1.8rem;
            }

            .close-model {
                background-color: transparent;
                color: red;
                border: none;
                font-weight: 800;
            }
        }
    }
}

       