.settings-page {
    position: relative;
    margin-bottom: 1rem;
    
    .page-info {
        position: relative;
        border-bottom: 1px solid rgb(48, 48, 48);

        padding: 4rem 5rem;

        .patterns {
            margin-left: 2rem;
        }

        .info-item {
            display: flex;

            h1, span {
                font-size: 1.8rem;
                margin-bottom: 1rem;
            }

            .name-page {
                width: 200px;
                background-color: transparent;
                color: #08FDD8;
                font-size: 1.5rem;
                border: 1px solid rgb(73, 73, 73);
                border-radius: 5px;
                margin-bottom: 5rem;
                transition: all .2s linear;

                &.error {
                    border: 1px solid rgb(255, 0, 0);
                }
            }
        }

        .pattern-name {
            color: red;
            transition: color .2s linear;
            &.true {
                color: #08FDD8;
            }
        }

        .btn-save {
            position: absolute;
            bottom: 0;
            right: 0rem;
            color: #000;
            border: none;
            padding: 1rem;
            font-weight: 500;
            background-color: #08FDD8;
            transition: all .2s linear;

            &:hover {
                background-color: lighten($color: #08FDD8, $amount: 30);
            }
        }

        .btn-select-pattern {
            display: block;
            margin-top: 1rem;
            color: #000;
            border: none;
            padding: 1rem;
            font-weight: 500;
            background-color: #08FDD8;
            transition: all .2s linear;

            &.error {
                background-color: red;

                &:hover {
                    background-color: lighten($color: red, $amount: 30);
                }
            }

            &:hover {
                background-color: lighten($color: #08FDD8, $amount: 30);
            }
        }
    }

    .model-window {
        position: absolute;
        display: flex;
        align-items: center;
        top: 0;
        left: 0;
        z-index: 100;
        width: 100%;
        height: 100vh;
        background: rgba(82, 82, 82, 0.9);

        .model {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            z-index: 1000;
            width: 1200px;
            height: 800px;
            background-color: rgb(15, 15, 15);
            border-radius: 5px;
            box-shadow: 0px 0px 20px 5px rgba(50, 50, 50, 1);
        }
    }
}