.page {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;

    .header {
        width: 100%;
        .header_footer {
            padding: .5rem 3rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1px solid red;

            .location {
                display: flex;
                justify-content: center;
                align-items: center;
                color: #000;
                svg {
                    width: 15px;
                    margin-right: 1rem;
                }
            }

            .connect {
                display: flex;
                justify-content: center;
                align-items: center;
                color: #000;
                svg {
                    width: 20px;
                    margin-right: 1rem;
                }

                p {
                    margin-right: 1rem;
                    &:last-child {
                        margin-right: 0;
                    }
                }
            }

            .social {
                display: flex;
                justify-content: center;
                align-items: center;
                svg {
                    width: 20px;
                    margin-right: 1rem;
                    path {
                        fill: #000;
                    }
                }
            }
        }

        .nav {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 1rem 3rem;

            .logo {
                width: 50px;
            }

            .nav_link {

                ul {
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    li {
                        color: #000;
                        margin-right: 2rem;

                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }
            }

            .basket_login {
                display: flex;
                justify-content: center;
                align-items: center;

                div {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color: #fff;
                    background-color: red;
                    padding: .5rem 1.5rem;
                    cursor: pointer;

                    svg {
                        width: 20px;
                        margin-right: 1rem;
                    }
                }

                a {
                    color: #fff;
                    background-color: red;
                    padding: .7rem 1.5rem;
                    margin-left: 1rem;
                    cursor: pointer;
                }
               
            }
        }
    }

    .main {
        width: 100%;
        display: flex;
        background-color: #fff;

        .sidebar {
            width: 250px;
            margin-right: 2rem;

            h1 {
                color: #fff;
                background-color: red;
                padding: 1rem 2rem;
            }

            ul {
                li {
                    color: #000;
                    background-color: rgb(170, 170, 170);
                    padding: 1rem 2rem;
                    border-bottom: 1px solid rgb(153, 153, 153);
                }
            }
        }

        .content {
            width: 100%;
            .bg_img {
                width: 100%;
                height: 200px;
                background: url(https://imgholder.ru/1306x200/8493a8/adb9ca&text=IMAGE+HOLDER&font=kelson);
                background-size: cover;
                background-repeat: no-repeat;
                margin-bottom: 2rem;
            }

            .store {
                width: 100%;
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
                grid-gap: 2rem;

                .store_item {
                    width: 280px;
                    height: 150px;
                    background: url(https://imgholder.ru/280x150/8493a8/adb9ca&text=IMAGE+HOLDER&font=kelson);
                    background-size: cover;
                    background-repeat: no-repeat;
                }
            }
        }
    }
}