.main {
    position: relative;
    width: 100%;
    height: 96vh;
    overflow-y: auto;
    overflow-x:hidden; 

    &.hidden {
        overflow-y: hidden;
    }

    .no-select-pattern {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%);
        color: rgb(73, 73, 73);
        font-size: 3rem;
    }

    &::-webkit-scrollbar {
        width: 4px; /* ширина для вертикального скролла */
        height: 8px; /* высота для горизонтального скролла */
        background-color: transparent;
    }
    
    /* ползунок скроллбара */
    &::-webkit-scrollbar-thumb {
        background-color: aqua;
        border-radius: 9em;
    }
}