.pattern-visit {
    width: 100%;
    height: 95%;

    .pattern-visit-header {
        width: 100%;
        height: 90px;

        .visit-header-info {
            height: 30px;
            background-color:red;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 2rem;
        }

        .visit-name {
            height: 60px;
            background-color:white;
            color: #000;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 4rem;
        }
    }

    .pattern-visit-content {
        width: 100%;
        height: 660px;
        background-color: rgb(51, 50, 50);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 3rem;
    }
}